html {-webkit-box-sizing: border-box;box-sizing: border-box;}
*, *::before, *::after { -webkit-box-sizing: inherit; box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  line-height: 1.6;
  color:  rgba(0,0,0,.87);
  background: #fefefe;
}



