.app {
  position: relative;
  min-height: 100vh;
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.header {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  background: #fff;
  -webkit-transition: all 170ms ease-out;
  -o-transition: all 170ms ease-out;
  transition: all 170ms ease-out;
}

.showingResults .header {
  top: 0;
  left: 0;
  padding: 20px 0;
  background: #539595;
}

.header .home {
  position: absolute;
  left: 20px;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
  opacity: 0;
  -webkit-transition: all 170ms ease-out;
  -o-transition: all 170ms ease-out;
  transition: all 170ms ease-out;
  text-decoration: none;
}

.header .home::before {
  color: #fff;
  content: 'home';
}

.showingResults .header .home {
  opacity: 1;
}

.content {
  -webkit-transition: all 350ms ease-out;
  -o-transition: all 350ms ease-out;
  transition: all 350ms ease-out;
  max-height: 0;
  width: 100%;
}

.showingResults .content {
  max-height: calc(100vh - 200px);
}



